/*--------------------------------------------------------------
# Header
--------------------------------------------------------------*/
.site-header {
  font-family: $font__accent;
  text-transform: uppercase;

  // fixed header
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 2000;

}

// .home .site-header,
.blog .site-header,
.single .site-header,
.page-template-find-us .site-header,
.archive .site-header,
.search-results .site-header,
.error404 .site-header {
  border-bottom: $border__solid;
  background: rgba(255, 255, 255, 0.8);
}

.page-template-about .site-header,
.home .site-header,
.single-adventure .site-header {
  border: 0;
  background: transparent;
}

/* This will ensure your site looks correct
 * when the admin bar is displaying.
--------------------------------------------- */
body.admin-bar .site-header {
  top: 32px;
}

.header {
  margin: 0 auto;
  display: flex;

  li {
    padding: $base__space-unit / 2 0.65rem;
  }

  .site-branding {
    a {
      height: 100%;
      display: flex;

      .header-logo {
        background-repeat: no-repeat;
        background-position: center center;
        background-size: 50px;
        width: 50px;
      }

      // img {
      //   height: 35px;
      //   align-self: center;
      //   width: 30%;
      // }
    }
  }
}

.blog .header-logo,
.single .header-logo,
.page-template-find-us .header-logo,
.archive .header-logo,
.search-results .header-logo,
.error404 .header-logo {
  background-image: url("../../public/assets/images/logos/inhabitent-logo-tent.svg");
  // background-repeat: no-repeat;
  // background-position: center center;
  // background-size: 50px;
  // width: 50px;
}

.page-template-about .header-logo,
.home .header-logo,
.single-adventure .header-logo {
  background-image: url("../../public/assets/images/logos/inhabitent-logo-tent-white.svg");
  // background-repeat: no-repeat;
  // background-position: center center;
  // background-size: 50px;
  // width: 50px;
}

.main-navigation {
  display: flex;
  justify-content: flex-end;

  .search-form {

    fieldset {
      border: none;
      padding: 0.75rem 0;

      .icon-search {
        color: $color__brand-green;

        &:hover {
          color: $color__text-main;
        }

        i {
          padding-right: 0;

        }
      }

      .search-submit {
        border: 0;
        background: none;
        box-shadow: none;
        padding: 0 0 0 0.65rem;
        width: 0;
      }
    }
  }
}

.page-template-about .main-navigation,
.home .main-navigation,
.single-adventure .main-navigation {

  a {
    color: $color__background-body;
    text-shadow: 2px 2px 4px #000000;

    &:hover {
      color: $color__brand-yellow;
    }
  }

  .search-form {
    fieldset {
      .icon-search {
        color: $color__background-body;
        text-shadow: 2px 2px 4px #000000;

        &:hover {
          color: $color__brand-yellow;
        }
      }
    }
  }
}



// .home .search-submit,
.blog .search-submit,
.single .search-submit,
.page-template-find-us .search-submit,
.archive .search-submit,
.search-results .search-submit {
  color: $color__brand-green;
}

.page-template-about .search-submit,
.home .search-submit {
  color: $color__background-body;
  box-shadow: 0 0 1rem rgba(0, 0, 0, .6);
}

// .home,
.blog,
.single,
.page-template-find-us,
.archive,
.search-results,
.error404 {
  /* This Should Be Equal to the Height of your
  * fixed header bar. */
  padding-top: 52.5px;
}

.home,
.page-template-about,
.single-adventure {
  padding-top: 0;
}

/* Navigation */